import React from 'react';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { publishEdit } from '../utils';

const Pill = SortableElement(({pillInfo}) => {

  return (
    <li className="pill" onClick={() => {publishEdit({pillText: pillInfo.pillText, pillColor: pillInfo.pillColor, pillDone: pillInfo.pillDone})}}>
      <div className={`${pillInfo.pillColor + "Pill"} pillContent ${pillInfo.pillDone + "Strike"}`}>
        <p className="pillText">{pillInfo.pillText}</p>
      </div>
    </li>
  );
});

export const PillList = SortableContainer(({pills}) => {
  return (
    <ul className="pillList">
      {pills.map((pillInfo, index) => (
        <Pill key={`item-${index}`} index={index} pillInfo={pillInfo} />
      ))}
    </ul>
  );
});



