import React, { Component } from 'react';
import MdStrike from 'react-icons/lib/md/format-strikethrough';
import MdDelete from 'react-icons/lib/md/delete';
import MdContentCopy from 'react-icons/lib/md/content-copy';

export class EditPill extends Component {
  constructor(props) {
    super(props);
    this.state = { 
        color: this.props.color,
        text:  this.props.text,
        pillDone: this.props.pillDone,
     };
  };

  updateInputValue = (evt) => {
    this.setState({
      text: evt.target.value
    });
  };

  updateColor = (color) => {
    this.setState({
      color: color
    })
  };

  render() {
    return (
      <div className="pillEditPanel">
        <div className={`${this.state.color + "Pill"} pillEditContent ${this.state.pillDone + "Strike"}`}>
          <div className="pillColorBtnSet">
            <button className="pillColorBtn bluePill" onClick={() => {this.updateColor('blue')}}>😀</button>
            <button className="pillColorBtn redPill" onClick={() => {this.updateColor('red')}}>🔥</button>
            <button className="pillColorBtn yellowPill" onClick={() => {this.updateColor('yellow')}}>☑️</button>
            <button className="pillColorBtn greenPill" onClick={() => {this.updateColor('green')}}>💬</button>
            <button className="pillColorBtn purplePill" onClick={() => {this.updateColor('purple')}}>💡</button>
          </div>
          <textarea className="pillEditText" ref="inputText" type="text" placeholder="Speak using voice keyboard..." value={this.state.text} onChange={this.updateInputValue} autoFocus></textarea>
          { this.props.editExist ? (
              <div className="pillOpBtnSet">
                <button className="pillOpBtn" onClick={() => {this.props.toggleEditPanel(false);this.props.deleteExistPill({oldPillText: this.props.oldPillText, oldPillColor: this.props.oldPillColor})}}><MdDelete /></button>
                <button className="pillOpBtn" onClick={() => {this.setState({pillDone: !this.state.pillDone})}}><MdStrike /></button>
                <button className="pillOpBtn" onClick={() => {this.refs.inputText.select(); document.execCommand('copy'); alert("😘 Copy Success");}}><MdContentCopy className="copyIcon" /></button>
              </div>
            ) : null }
          <button className="pillYNBtn pillEditCancelBtn" onClick={() => {this.props.toggleEditPanel(false);}}>Cancel</button> 
          { this.props.editNew ? <button className="pillYNBtn pillEditConfirmBtn" onClick={() => {this.props.addOnePill({pillText: this.state.text, pillColor: this.state.color, pillDone: this.state.pillDone});this.props.toggleEditPanel(false);}}>Done</button> : null }
          { this.props.editExist ? <button className="pillYNBtn pillEditConfirmBtn" onClick={() => {this.props.editExistPill({oldPillText: this.props.oldPillText, newPillText: this.state.text, oldPillColor: this.props.oldPillColor, newPillColor: this.state.color, newPillDone: this.state.pillDone});this.props.toggleEditPanel(false);}}>Update</button> : null }
        </div>
      </div>
    );
  }

}


