var Pubsub = require('pubsub-js');

export const handleClick = (pillInfo) => {
  console.log(pillInfo.pillText + " - " + pillInfo.pillColor);
}

/**
 * publish a request evoking edit window
 * @param {object} pillInfo {pillText, pillColor} 
 * @return {null}
 */
export const publishEdit = (pillInfo) => {
  Pubsub.publish('editOnePill', {
    pillText: pillInfo.pillText,
    pillColor: pillInfo.pillColor,
    pillDone: pillInfo.pillDone
  });
}


