import React, { Component } from 'react';
import { EditPill } from './editPill';
var Pubsub = require('pubsub-js');
// import { EditPill } from './editPill';
import {arrayMove} from 'react-sortable-hoc';
import {PillList} from './pill';

export class PillPanel extends Component {

  constructor(props) {
    super(props);
    this.state = { 
        pills: this.props.pills.length ? this.props.pills : 
          [
            {pillText:'IdeaPill is a todo tool following Eisenhower Decision Matrix.', pillColor:'purple', pillDone:false},
            {pillText:'😀 Blue = Important but Not Urgent Task.', pillColor:'blue', pillDone:false},
            {pillText:'🔥 Red = Important and Urgent Task.', pillColor:'red', pillDone:false},
            {pillText:'☑️ Yellow = Urgent but Not Important Task.', pillColor:'yellow', pillDone:false},
            {pillText:'💬 Green = Communication / Contact Others.', pillColor:'green', pillDone:false},
            {pillText:'💡 Purple = Idea. Press the pill button below to add the first pill', pillColor:'purple', pillDone:false},
          ],
        showEditPanel: false,
        oldPillText: "",
        oldPillColor: "",
        oldPillDone: false
     };
  };

  onSortEnd = ({oldIndex, newIndex}) => {
    this.setState({
      pills: arrayMove(this.state.pills, oldIndex, newIndex),
    });

    // store into cache here
    // console.log(this.state.pills);
    localStorage.setItem("pills", JSON.stringify(this.state.pills));
  };

  componentWillMount = () => {

    var that = this;

    // subscribe add pill event
    this.addPillSubscribe = Pubsub.subscribe('addOnePill', function(msg, data) {
      console.log("data: " + data.pillList);
      that.setState({
        pills: data.pillList,
      });
    });

    // subscribe edit existing pill event
    this.editPillSubscribe = Pubsub.subscribe('editOnePill', function(msg, data) {
      that.setState({
        showEditPanel: true,
        oldPillText: data.pillText,
        oldPillColor: data.pillColor,
        oldPillDone: data.pillDone
      });
    });
  };

  componentWillUnmount = () => {
    Pubsub.unsubscribe(this.addPillSubscribe);
    Pubsub.unsubscribe(this.editPillSubscribe);
  };

  editExistPill = (pillInfo) => {
    // find old pill 
    var idx = this.state.pills.findIndex(ele => ele.pillText === pillInfo.oldPillText && ele.pillColor === pillInfo.oldPillColor);
    if(idx === -1) {console.log("The pill to be deleted is not found..."); return;}

    // update pill and save to cache
    var pills = this.state.pills;
    pills[idx] = {pillText: pillInfo.newPillText, pillColor: pillInfo.newPillColor, pillDone: pillInfo.newPillDone};
    localStorage.setItem("pills", JSON.stringify(pills));

    // set pill state to re-render
    this.setState({
      pills: pills
    });
  };

  deleteExistPill = (pillInfo) => {
    // find old pill 
    var idx = this.state.pills.findIndex(ele => ele.pillText === pillInfo.oldPillText && ele.pillColor === pillInfo.oldPillColor);
    if(idx === -1) {console.log("The pill to be deleted is not found..."); return;}
    if(!confirm("Confirm delete 💊？")) return;

    // delete pill by index and save to cache
    var pills = this.state.pills;
    pills.splice(idx, 1);
    localStorage.setItem("pills", JSON.stringify(pills));

    // set pill state to re-render
    this.setState({
      pills: pills
    });
  }

  toggleEditPanel = (state) => {
    this.setState({
      showEditPanel: state
    });
  };
  
  render() {
    return (
      <div>
        { this.state.showEditPanel ? <EditPill color={this.state.oldPillColor} text={this.state.oldPillText} pillDone={this.state.oldPillDone} editExistPill={this.editExistPill} deleteExistPill={this.deleteExistPill} toggleEditPanel={this.toggleEditPanel} editNew={false} editExist={true} oldPillText={this.state.oldPillText} oldPillColor={this.state.oldPillColor} oldPillDone={this.state.oldPillDone} /> : null }
        <PillList pills={this.state.pills} onSortEnd={this.onSortEnd} helperClass="sortableHelper" pressDelay={200} />
      </div>
    )
  };
}


