import React, { Component } from 'react';
import {PillPanel} from './components/pillPanel';
import {AddPill} from './components/addPill';
import './App.css';

/* cache for user data */
// detect if support H5 web storage
if (typeof(Storage) === "undefined") { 
  if (confirm("Upload browser to use this APP :)") === true) {
    location.assign("https://www.google.com/chrome/");
  } else {
    // load page anyway...
  }
};

// initialize local storage for pills and cards
localStorage.getItem("pills") === null && localStorage.setItem("pills", "[]");
localStorage.getItem("cards") === null && localStorage.setItem("cards", "[]");
var pills = JSON.parse(localStorage.getItem("pills"));
//var cards = JSON.parse(localStorage.getItem("cards"));

// APP
class App extends Component {
  render() {
    return (
      <div>
        <PillPanel pills={pills} />
        <AddPill />
      </div>
    );
  }
}

export default App;



