import React, { Component } from 'react';
import { EditPill } from './editPill';
var Pubsub = require('pubsub-js');
import PillImg from '../../public/pillBtn.png';

export class AddPill extends Component {
  
  constructor(props) {
    super(props);
    this.state = { 
      color: "blue",
      text:  "",
      pillDone: false,
      showEditPanel: false
    };
    this.addOnePill = this.addOnePill.bind(this);
    this.toggleEditPanel = this.toggleEditPanel.bind(this);
  };
  
  /**
   * Add one new pill, update the local storage, and set App pill state
   * @return {array} ["item1", "item2"]
   * @param {json} pillInfo {pillText, pillColor}
   */
  addOnePill = (pillInfo) => {

    // if pill text is null or "", early stop
    var pillText = pillInfo.pillText;
    if(pillText === "" || pillText === null) {
      alert("😖 Pill cannot be empty.");
      return;
    }

    // if pill already exists, early stop
    var idx = JSON.parse(localStorage.getItem("pills")).findIndex(ele => ele.pillText === pillInfo.oldPillText && ele.pillColor === pillInfo.oldPillColor);
    if(idx !== -1) {
      alert("😜 Pill already existed."); 
      return;
    }

    // judge if local storage is ""
    var pills = [pillInfo];
    if(localStorage.getItem("pills")) {
      pills = JSON.parse(localStorage.getItem("pills"));
      pills.unshift(pillInfo);   
    }
    localStorage.setItem("pills", JSON.stringify(pills));

    // publish an event
    Pubsub.publish('addOnePill', {
      pillList: pills,
    });

    //setAppPillState(pills);
    return pills;
  };

  toggleEditPanel = (state) => {
    this.setState({
      showEditPanel: state
    });
  };

  render() {
    return (
      <div className="pillBanner">
        { this.state.showEditPanel ? <EditPill color={this.state.color} text={this.state.text} pillDone={this.state.pillDone} addOnePill={this.addOnePill} toggleEditPanel={this.toggleEditPanel} editNew={true} editExist={false} /> : null }
        <img src={PillImg} className="pillBtn" onClick={() => {this.toggleEditPanel(true)}} alt="Add a Pill" />
      </div>
    );
  }
  
}


